.menu {
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include onSmallDesktop {
      flex-direction: row;
    }
  }

  &__icon {
    display: block;
    height: 8px;
    width: 18px;
    background-color: transparent;
    background-image: url(../images/menu.svg);

    @include onSmallDesktop {
      opacity: 0;
    }
  }

  &__list {
    position: fixed;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    opacity: 0;

    list-style: none;

    @include onSmallDesktop {
      opacity: 1;
      flex-direction: row;
    }
  }

  &__item {
    display: block;
    margin-right: 48px;

    font-family: "Popins", sans-serif;
    font-weight: 700;
    font-size: 13px;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
  }
}
