.slider {
  height: 390px;
  width: 100%;
  bottom: 0;
  right: 0;
  background-image: url(../images/slider/slide-img-1.jpg);
  background-repeat: no-repeat;

  &__button {
    background-color: $menu-color;
    height: 32px;
    width: 32px;
    border-radius: 50%;

    &--right {
      color: $fon-header;
    }

    &--left {
      color: $c-gray;
    }
  }
}
