.header {
  position: relative;
  background-color: $fon-header;
  border-radius: 0 0 20px 20px;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    box-sizing: border-box;
    height: 100vh;
    padding: 20px 20px 48px;
  }

  &__top {
    display: grid;
    grid-template-columns: 46px 18px 1fr;
    justify-content: space-between;
    align-items: center;
    gap: 36px;
  }

  &__title {
    font-weight: 600;
    font-size: 52px;
    line-height: 120%;
    letter-spacing: -2px;
  }

  &__subtitle {
    font-family: "Open Sans", sans-serif;
    font-size: 18px;
  }
}
